import React, { Component } from 'react';
import Navigation from './components/Navigation/Navigation';
import Footer from './components/Footer/Footer';
import About from './components/About/About';
import Home from './components/Home/Home';
import Films from './components/Films/Films';
import { connect } from 'react-redux';
import { requestFilms } from './actions.js';

const mapStateToProps = state =>{
  return {
    films: state.requestFilms.films.results,
    isFilmsPending: state.requestFilms.isFilmsPending,
    filmsError: state.requestFilms.filmsError
  };
}

const mapDispatchToProps = (dispatch) =>{
  return {
    onRequestFilms: () => dispatch(requestFilms())
    };
  }

class App extends Component {

  constructor(){
    super();
    this.state = {
      route:'home'
          }
  }

  changeRoute = (route) =>{
    this.setState({route: route});
  }

  componentDidMount(){
    this.props.onRequestFilms()
  }

  render() {
   const { route } = this.state;

    let body = '';

   switch(route){
    case 'home':
      body = <Home/>
      break;
    case 'characters':
      body = <h1 className='yellow'>Characters</h1>;
      break;
    case 'planets':
      body = <h1 className='yellow'>planets</h1>;
      break;
    case 'films':
      const { films, isFilmsPending } = this.props;
      console.log(films)  
      body = <Films films={films}/>;
      break;
    case 'about':
      body = <About/>
      break;
   }


    return (
      <div className='App'>
        <Navigation changeRoute={this.changeRoute} />
        {body}
        {/*<Scroll>
        </Scroll>*/}

        
          {/*<div>
           <SearchBox/>
            <Characters/>
            <Films/>
            <Planets/>
          </div>
*/}        
        <Footer/>  
      </div>
    );
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(App);

